
import Scroll from '../../../base/scroll/scroll'
import Step from '../../../base/step/step'
import MButton from '../../../base/button/button'
import Info from '../../../base/info/info'
import Pwdbox from '../../../base/pwdbox/pwdbox'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import { userInfoMixin } from '../../../assets/js/mixin'
export default {
name: "userQq",
mixins:[userInfoMixin],
components: {
Scroll,
XInput,
Group,
Step,
MButton,
Info,
Pwdbox
},
watch: {
userMsg(val) {
if (val.length == 6) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},
pwd(val) {
if (val.length === this.maxLength) {
this.btnNexteState = true
return false
}
this.btnNexteState = false
},
rePwd(val) {
if (val === this.pwd) {
this.btnSureState = true
return false
}
this.btnSureState = false
}
},
computed: {
userPwdPhone: {
get: function() {
let reg = /^(\d{3})\d{4}(\d{4})$/;
let tel = this.userInfo.telephone.replace(reg, "$1^-^$2");
return tel
},
set: function(v) {
this.userPhone = v
}
}
},
data() {
return {
stepArray: ['', '', ''],
infos: [
'',
''
],
stepIndex: 0,//
btnSaveState: false,
btnCodeState: true,
btnNexteState: false,
btnSureState: false,
userMsg: '',
disabled: '',
time: 60,
codeText: '',
timer: null,
pwd: '',//
maxLength: 6,
rePwd: '',
}
},
methods: {
getCode() {
if (this.btnCodeState) {
this.btnCodeState = false
this.$axios.post('/api/sms/send', {
telephone: this.userPhone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.timer = setInterval(this.timeGo, 1000)
}
}).catch((error) => {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: '',
})
})
}
},
timeGo() {
if (this.time >= 0) {
this.codeText = `${this.time}(s)`
this.time--
} else {
this.codeText = ''
this.time = 60
clearInterval(this.timer)
this.timer = null
this.btnCodeState = true
}
},
nextOne() {
this.$axios.post('/api/sms/verify', {
telephone: this.userPhone,
code: this.userMsg,
type: 0
}).then((response) => {
if (response.data.code !== '200') {
this.$vux.alert.show({
title: '',
content: response.data.message,
})
} else {
this.stepIndex = 1
}
}).catch((error) => {
console.log(error)
})
},
nextTwo() {
this.stepIndex = 2
this.rePwd = ''
},
sure() {
this.$axios.post('/api/user/resetDepositWord', {
telephone: this.userPhone,
depositPassword: md5(this.pwd),
}).then((response) => {
if (response.data.code !== '200') {
this.$vux.alert.show({
title: '',
content: response.data.message,
})
} else {
this.$vux.toast.show({
text:'',
type:'success',
time:1000,
onHide(){
_this.$router.push({ name: 'user' })
}
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



