export default {
  name: "component_name",
  props:{
    title:{
      type:String,
      default:''
    },
    length:{
      type:Number,
      default:6
    },
    value:{
      type:String,
      default:''
    }
  },
  created () {
    this.currentValue = this.value
  },
  data() {
    return {
      currentValue:'',
      msgLength:0
    }
  },
  watch: {
    value (curVal) {
      this.currentValue = curVal
    },
    currentValue (curVal) {
      if (/[^\d]/g.test(curVal)) {
        this.currentValue = this.currentValue.replace(/[^\d]/g, '');
      } else {
        this.msgLength = curVal.length;
        this.$emit("input",curVal)
      }
    }
  },
  methods: {
    focus() {
      this.$refs.pwd.focus()
    }
  }
}



